<!--  -->
<template>
    <div class="content">
        <div class="taxi-content bg">
            <timepick class="border-bottom-1px"></timepick>
            <div class="taxi-location border-top-1px">
                <!--  -->
                <location-com type='from'></location-com>
                <!--  -->
                <location-pick type='to' :data="LocationData" :limit="3"></location-pick>
            </div>
            <!--  -->
            <div v-if="show">
              <div>
                <FCService></FCService>
              </div>
              <div class="flex-content">
                  <changePassenger class="flex-item gap"></changePassenger>
                  <changeCarType class="flex-item gap"></changeCarType>
              </div>
              <div class="flex-content border-top-1px">
                  <changePayType class="flex-item"></changePayType>
              </div>
              <div class="flex-content border-top-1px">
                  <estimate class="flex-item"></estimate>
              </div>
          </div>
        </div>
        <cube-button type="submit" :disabled="isAllow" @click="sendOrder()"></cube-button>
    </div>
</template>
<script>
import { mapState } from 'vuex'
const locationCom = () => import('@/components/common/locationCom.vue')
const locationPick = () => import('@/components/common/locationPick.vue')
const timepick = () => import('@/components/common/timePick.vue')
const flight = () => import('@/components/flight/flight.vue')
const FCService = () => import('@/components/common/FCService.vue')
const changePassenger = () => import('@/components/common/changePassenger.vue')
const changeCarType = () => import('@/components/common/changeCarType.vue')
const changePayType = () => import('@/components/common/changePayType.vue')
const estimate = () => import('@/components/common/estimate.vue')
export default{
  name: 'dropOff',
  data () {
    return {
      debounce: null
    }
  },
  mounted () {
    if (this.state.order.orderType === 3) {
      if (!this.state.locationFrom) {
        this.$store.dispatch('switch_location', {name: 'location', val: this.state.Geolocation})
      }
    }
  },
  computed: mapState({
    state (state) {
      // state
      return state
    },
    show (state) {
      if (state.location.locationFrom && state.location.locationTo) {
        this.$store.dispatch('post_disAndDura').then((data) => {
          if (data.errCode === 2004) {
            this.$createDialog({
              type: 'confirm',
              title: '',
              content: '' + data.msg + '',
              confirmBtn: {
                text: '',
                active: true,
                disabled: false,
                href: 'javascript:;'
              },
              cancelBtn: {
                text: '',
                active: false,
                disabled: false,
                href: 'tel:059296363'
              },
              onConfirm: () => {},
              onCancel: () => {}
            }).show()
            return
          }
          if (!data.success) {
            this.$createDialog({
              type: 'alert',
              title: '',
              content: data.msg
            }).show()
          }
        })
      }
      return state.location.locationFrom && state.location.locationTo
    },
    isAllow (state) {
      return !(state.location.locationFrom && state.location.locationTo)
    },
    LocationData (state) {
      return state.staticData.loca_data
    }
  }),
  methods: {
    sendOrder () {
      var self = this
      window.clearTimeout(this.debounce)
      this.debounce = window.setTimeout(function () {
      // 
        self.$store.dispatch('orderRequest').then((data) => {
          if (data.errCode === 2004) {
            self.$createDialog({
              type: 'confirm',
              title: '',
              content: '' + data.msg + '',
              confirmBtn: {
                text: '',
                active: true,
                disabled: false,
                href: 'javascript:;'
              },
              cancelBtn: {
                text: '',
                active: false,
                disabled: false,
                href: 'tel:059296363'
              },
              onConfirm: () => {},
              onCancel: () => {}
            }).show()
            return
          }
          if (data.noSubmit || !data.success) {
            self.$createDialog({
              type: 'alert',
              title: '',
              content: data.msg
            }).show()
          }
        })
      // ======
      }, 500)
    }
  },
  components: {
    locationCom,
    locationPick,
    timepick,
    FCService,
    flight,
    changePassenger,
    changeCarType,
    changePayType,
    estimate
  }
}
</script>
<style lang='less'>
@import "../../assets/css/less/variable.less"; 
.taxi-content {
  display: block;
  margin-bottom: 10px;
  color: #999;
  &.bg {
    background-color: #fff;
    .boxShadow()
  }
  &:after {
    .clear()
  }
}


</style>


